import React, { useState, useEffect } from "react";
import { Box, Button } from "@material-ui/core";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import "react-alice-carousel/lib/alice-carousel.css";
import InfoCard from "appComponents/layout/InfoCard";
import RatingStar from "appComponents/interaction/RatingStar";
import ImgUpload from "appComponents/interaction/ImgUpload";
import TextArea from "appComponents/interaction/TextArea";
import { onResponseSuccess, requestBiztApi } from "utility/connectivity";

const useStyles = makeStyles((theme) => ({
  starTitle: {
    textAlign: "center",
    fontSize: "17px",
    fontWeight: "bold",
    marginBottom: "20px",
  },
  fileUpload: {
    "& .MuiButton-root": {
      minWidth: "100px",
      minHeight: "100px",
      borderRadius: "2px",
      boxShadow: "none",
    },
  },
  next: {
    backgroundColor: `${theme.palette.primary.main}`,
    color: "white",
    borderRadius: "1px",
    width: "100%",
  },
  rateStar: {
    textAlign: "right",
    boxSizing: "border-box",
  },
  wrapper: {
    "& .MuiGridListTile": {
      height: "auto",
      maxWidth: "100%",
      background: "red",
    },
    "& .MuiGridList-root": {
      display: "flex",
      padding: 0,
      flexWrap: "wrap",
      listStyle: "none",
      overflow: "auto",
      flexDirection: "column",
      margin: 0,
    },
    "& .alice-carousel__stage-item img": {
      height: "auto",
      width: "100%",
      maxWidth: "100%",
    },
    "& .MuiRating-sizeSmall": {
      fontSize: "3.125rem",
    },
    "& .MuiRating-root": {
      width: "100%",
      justifyContent: "center",
    },
  },
}));

const customStyles = makeStyles((theme) => ({
  list_box: {
    width: "100%",
    display: "flex",
    marginTop: "10px",
  },
  info: {
    width: "50%",
  },
}));

function Templete(props) {
  const classes = useStyles();
  const InfoCardClasses = customStyles();
  const [reservation, reservationlist] = useState();

  const [review, setReview] = useState(null);
  const [star, setStar] = useState();
  const [input, setInput] = useState();

  const [reviewImgs, setReviewImgs] = useState([]);
  let reviewImgFiles = [];

  const [textValue, setTextValue] = React.useState("");

  const handleChange = (e) => {
    setTextValue(e.target.value);
  };

  useEffect(() => {
    requestBiztApi("/api/reviews", { body: { desc: textValue, rating: star } });
    requestBiztApi(
      "/api/reservation",
      null,
      onResponseSuccess(reservationlist)
    );
  }, []);

  return (
    <Box className="root">
      {reservation && (
        <Box className="root" style={{ marginTop: "10px" }}>
          <Box className="body">
            <InfoCard
              title={reservation[0]?.officeData.officeName}
              details={[
                {
                  category: "이용호실",
                  content: reservation[0]?.reservationId,
                },
                {
                  category: "예약일",
                  content: `${reservation[0].start_datetime}~${reservation[0].end_datetime}`,
                },
                {
                  category: "예약시간",
                  content: `${reservation[0].start_datetime}~${reservation[0].end_datetime}`,
                },
                {
                  category: "결제금액",
                  content: `${reservation[0]?.paymentDetails.price}원`,
                },
              ]}
            />
            <Box
              style={{
                height: "2px",
                margin: "20px 0",
                backgroundColor: "gainsboro",
              }}
            />
            <Box className="FlexCenter">
              <div>
                <p className={classes.starTitle}> 비지트 이용은 어떠셨나요?</p>
                <div className="FlexCenter">
                  <RatingStar classes={classes} size="large"></RatingStar>
                </div>
              </div>
            </Box>
            <Box
              style={{
                height: "2px",
                margin: "20px 0",
                backgroundColor: "gainsboro",
              }}
            />
            <ImgUpload
              imgs={reviewImgs}
              setImgs={setReviewImgs}
              fileContainer={reviewImgFiles}
            />
            <Box
              style={{
                height: "2px",
                margin: "20px 0",
                backgroundColor: "gainsboro",
              }}
            />
            <Box style={{ margin: "20px" }}>
              <TextArea
                placeholder="사용자 리뷰를 입력해주세요."
                className={classes.Textarea}
                value={review}
                onchange={(e) => setReview(e.target.value)}
                style={{ padding: "0px" }}
              ></TextArea>
            </Box>

            <Box className="foot">
              <Link to="/mypage/reviews/of/me">
                <Button className="wideBtnWrapper">
                  <span className="wideBtn">등록하기</span>
                </Button>
              </Link>
            </Box>
          </Box>
        </Box>
      )}
    </Box>
  );
}

export default Templete;
