import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
// MessageBox component
import { useRef } from "react";
import { useHistory } from "react-router-dom";
import OfficeCard from "appComponents/interaction/MessOfficeCard";
import MessDetail from "pages/Messages/MessDetail";

import { requestBiztApi } from "utility/connectivity";

import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
// 파트너 이미지
import office from "Img/office.png";

// RCE CSS
import "react-chat-elements/dist/main.css";

const ChatDetailStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
    padding: "0",
    position: "relative",
  },
  chat: {
    width: "100%",
    maxWidth: "450px",
    height: "65px",
    position: "fixed",
    bottom: "56px",
    backgroundColor: theme.palette.primary.gray,
    "& form": {
      width: "100%",
      display: "flex",
      justifyContent: "center",
      paddingTop: "15px",
    },
  },
  input_wrap: {
    width: "400px",
    position: "relative",
  },
  input: {
    width: "400px",
    height: "35px",
    borderRadius: "18px",
    paddingLeft: "20px",
    border: `1px solid ${theme.palette.primary.lightdark}`,
    paddingRight: "35px",
  },
  input_btn: {
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    position: "absolute",
    right: "8px",
    top: "5px",
    backgroundColor: theme.palette.primary.main,
    color: theme.typography.light,
    border: "none",
  },
  resdeny: {
    position: "absolute",
    top: "0",
    left: 0,
    width: "100%",
  },
  display: {
    display: "block",
  },
  none: {
    display: "none",
  },
}));

var scrollDownFlag = false;

export default function ChatDetail({ scrollRef }) {
  const classes = ChatDetailStyles();
  const history = useHistory();
  const [chatroom, chatroomlist] = useState(null);
  const [chat_value, set_chat_value] = useState();
  const [twoButton, setTwoButton] = useState(true); //user이면 false, partner면 true
  let pathname = history.location.pathname;
  //파트너면 버튼두개, 이용자면 버튼한개
  useEffect(() => {
    if (pathname.split("/")[1] == "partner") {
      setTwoButton(true);
    } else {
      setTwoButton(false);
    }
  }, [pathname]);
  const [card, setcard] = useState([
    {
      who: "user",
      src: "/static/images/avatar/1.jpg",
      title: "파트너1",
      body: "확인후, 승인 완료 예정입니다.",
      date: "2021.05.10 10:11 오전",
      info_card: true,
      list_title: "방이동 오피스 101",
      infoTxt: [
        { txt_left: "이용호실", txt_right: "개인1호실" },
        { txt_left: "이용좌석", txt_right: "B-5" },
        { txt_left: "예약일", txt_right: "21.05.17(월)~21.05.19(수)/3일간" },
        { txt_left: "예약시간", txt_right: "09:00~12:00/3시간" },
        { txt_left: "결제액", txt_right: "25,000원" },
      ],
      twoButton: twoButton,
      reservationStatus: "승인 대기중",
    },
    {
      who: "other",
      src: "/static/images/avatar/1.jpg",
      title: "kimm123",
      body: "답변감사합니다:)",
      date: "2021.05.10 10:11 오전",
      info_card: false,
    },
  ]);

  const inputRef = useRef();

  function Message(e) {
    e.preventDefault();
    // console.log(inputRef.current["chat"].value);
    var now = new Date();
    setcard(
      card.concat([
        {
          who: "user",
          src: "/static/images/avatar/1.jpg",
          title: "파트너",
          body: inputRef.current["chat"].value,
          date:
            now.toLocaleDateString("ko-KR") +
            " " +
            now.toLocaleTimeString("it-IT"),
          info: false,
        },
      ])
    );
    set_chat_value("");

    scrollDownFlag = true;
  }
  function handleKeyPress(e) {
    if (e.key === "Enter") {
      e.preventDefault();
      Message(e);
    }
  }
  useEffect(() => {
    if (scrollDownFlag)
      scrollRef?.current?.scrollTo(0, scrollRef?.current?.scrollHeight);
  }, [card]);

  return (
    <div className={classes.root} style={{ marginBottom: "50px" }}>
      <OfficeCard
        src={office}
        office="방이동 오피스101"
        location="방이동"
        people="최대15인"
      ></OfficeCard>
      {card.map((item, idx) => {
        let cardProps = {
          who: item.who,
          src: item.src,
          title: item.title,
          body: item.body,
          date: item.date,
          info_card: item.info_card,
          list_title: item.list_title,
          infoTxt: item.infoTxt,
          twoButton: twoButton,
          reservationStatus: item.reservationStatus,
        };
        return <MessDetail {...cardProps}></MessDetail>;
      })}

      <div className={classes.chat}>
        <form
          className={classes.cat_box}
          noValidate
          autoComplete="off"
          ref={inputRef}
        >
          <div className={classes.input_wrap}>
            <input
              type="text"
              className={classes.input}
              name="chat"
              value={chat_value}
              onChange={(e) => {
                set_chat_value(e.target.value);
              }}
              onKeyPress={(e) => {
                handleKeyPress(e);
              }}
              placeholder="메세지 입력하기"
            ></input>
            <div className={classes.input_btn} onClick={Message}>
              <KeyboardArrowUpIcon></KeyboardArrowUpIcon>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
