import React, { useState, useEffect } from "react";
import { useRouteMatch, useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import { Box, Divider } from "@material-ui/core";
import ReservationCard from "pages/Partner/Reservation/ReservationCard";
import { requestBiztApi } from "utility/connectivity";
import "react-spring-bottom-sheet/dist/style.css";

// 파트너 이미지
const useStyles = makeStyles((theme) => ({
  root: {
    overflowX: "hidden",
  },
  Divider: {
    width: "95%",
    margin: "0 auto",
  },

  list_left_img: {
    width: "92px",
    height: "92px",
  },
  UnderlineBox: {
    "&:last-child": {
      "& .MuiDivider-root": {
        backgroundColor: theme.typography.light,
      },
    },
  },
}));

export default function Card(props) {
  const classes = useStyles();
  const { path } = useRouteMatch(); // /partner/reservation
  const history = useHistory();
  const [reservation, reservationlist] = useState(null);
  const historyPath = history.location.pathname;
  useEffect(() => {
    if (historyPath === path) {
      requestBiztApi("/api/reservation", null, reservationlist);
    }
  }, [historyPath, path]);

  function CalculateTime(from, to) {
    return (
      from.toLocaleDateString("en", { month: "2-digit" }) +
      "/" +
      from.toLocaleDateString("en", { day: "2-digit" }) +
      " " +
      from.toLocaleTimeString("it-IT", { hour: "2-digit", minute: "2-digit" }) +
      " ~ " +
      to.toLocaleDateString("en", { month: "2-digit" }) +
      "/" +
      to.toLocaleDateString("en", { day: "2-digit" }) +
      " " +
      to.toLocaleTimeString("it-IT", { hour: "2-digit", minute: "2-digit" })
    );
  }
  return (
    <Box className={classes.root}>
      <Box>
        {reservation &&
          reservation.map((reservationData, idx) => {
            let cardProps = {
              img: "/" + reservationData.officeData.images[0],
              title: reservationData.seatNumber,
              mess_link: "/partner/message",
              leftFunction: props.leftFunction,
              rightFunction: props.rightFunction,
              status: "after",
              review: true,
              cardDetails: [
                {
                  category: "예약번호",
                  content: reservationData.reservationId,
                },
                {
                  category: "이용호실",
                  content: reservationData.officeData.officeName,
                },
                {
                  category: "예약시간",
                  content: CalculateTime(
                    new Date(reservationData.reservedPeriod.from),
                    new Date(reservationData.reservedPeriod.to)
                  ),
                },
                {
                  category: "결제액",
                  content: reservationData.paymentDetails.price + "원",
                },
              ],
            };
            return (
              <div key={idx} className={classes.UnderlineBox}>
                <ReservationCard {...cardProps}></ReservationCard>
                <Divider className={classes.Divider}></Divider>
              </div>
            );
          })}
      </Box>
    </Box>
  );
}
