import React, { useState, useEffect, useRef, useContext } from "react";
import { Switch, Route, useRouteMatch } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import { Box } from "@material-ui/core";
import { BottomSheet } from "react-spring-bottom-sheet";
import RangeCalendar from "appComponents/interaction/RangeCalendar";
import CarouselDate from "pages/Partner/Reservation/CarouselDate";
import Select100 from "appComponents/interaction/Select100";
import Card from "pages/Partner/Reservation/Card";
import "react-spring-bottom-sheet/dist/style.css";
import { TTBContext } from "utility/contexts";
import SettingsIcon from "@material-ui/icons/Settings";
// 파트너 이미지
const useStyles = makeStyles((theme) => ({
  bottomSheet: {
    "& [data-rsbs-overlay], [data-rsbs-backdrop], [data-rsbs-root]:after": {
      zIndex: 101,
      bottom: "56px",
    },
    "&:after": {
      zIndex: 101,
      bottom: "56px",
    },
    "& [data-rsbs-header]": {
      height: "75px",
    },
    "& [data-rsbs-content]": {
      overflow: "unset",
    },
  },
  daterangeicon: {
    width: "20px",
    height: "20px",
    marginTop: "3px",
    color: theme.palette.primary.main,
  },
  popup: {
    width: "100%",
    "& .MuiDialog-container": {
      width: "100%",
    },
    "& .MuiDialog-paper": {
      margin: "0",
    },
  },
  calendar_title: {
    width: "100%",
    height: "50px",
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    color: theme.typography.main,
    fontWeight: theme.typography.fontWeightMedium,
    fontSize: "15px",
  },
  calendar_wrap: {
    width: "100%",
  },
  title: {
    paddingRight: "10px",
  },
  reset: {
    color: theme.typography.sub,
    fontSize: "13px",
    paddingLeft: "10px",
    fontWeight: theme.typography.fontWeightMedium,
  },
  close: {
    color: theme.palette.primary.sub,
    fontSize: "13px",
    paddingRight: "10px",
    fontWeight: theme.typography.fontWeightMedium,
  },
  fre_wrap: {
    padding: "30px 0 0 0",
    display: "block",
    textDecoration: "none",
    color: theme.typography.color,
  },
  fre_title: {
    paddingLeft: "26px",
    paddingBottom: "30px",
    fontWeight: "600",
    letterSpacing: "-1px",
    fontSize: "15px",
  },
  se_title: {
    paddingLeft: "26px",
    fontWeight: "600",
    letterSpacing: "-1px",
    fontSize: "15px",
  },
  fre_select: {
    width: "90%",
    margin: "0 auto",
  },
}));

export default function Reservation(props) {
  const classes = useStyles();
  const sheetRef = useRef();
  const { path } = useRouteMatch(); // /partner/reservation
  const setTtbOptional = useContext(TTBContext);

  const [open, setOpen] = useState(false);
  const [startDate, setstartDate] = useState(new Date());
  const [duration, setDuration] = useState(6);

  //toptoolbar
  useEffect(() => {
    setTtbOptional({
      path: path,
      button: (
        <div onClick={popupOpen}>
          <SettingsIcon
            className={classes.daterangeicon}
            fontSize="small"
          ></SettingsIcon>
        </div>
      ),
    });
  }, []);

  const nowDay = {
    startDate: new Date(),
    endDate: new Date(),
    key: "selection",
  };
  // 기간설정 rangecalendar
  const [selection, setSelection] = useState(nowDay);

  function popupOpen() {
    setOpen(!open);
  }
  // 기간 지정시에 저장클릭후 rendering
  function popupClose() {
    setOpen(!open);
    if (selection) {
      var start_date = selection.startDate;
      var end_date = selection.endDate;

      //시작일부터 몇일인지 구하기
      var elapsedMSec = end_date.getTime() - start_date.getTime();
      var duration = elapsedMSec / 1000 / 60 / 60 / 24;

      setstartDate(start_date);
      setDuration(duration);
    }
    reset();
  }
  //select office, room 선택값
  const [office, setOffice] = useState();
  const [room, setRoom] = useState();
  //office select 클릭후 room select 보이게
  const [officeSelected, setOfficeSelected] = useState(false);

  function officeChange(selectedOption) {
    setOffice(selectedOption);
    selectedOption.value != "전체 오피스"
      ? setOfficeSelected(true)
      : setOfficeSelected(false);
  }
  function reset() {
    setSelection(nowDay);
    setOffice({ value: "전체 오피스", label: "전체 오피스" });
    setRoom({ value: "전체 호실", label: "전체 호실" });
    setOfficeSelected(false);
  }

  return (
    <Switch>
      <Route path={path}>
        <CarouselDate startDay={startDate} duration={duration} />
        <Card />
        <BottomSheet
          open={open}
          skipInitialTransition
          blocking={false}
          ref={sheetRef}
          onDismiss={() => {
            setOpen(!open);
            reset();
          }}
          className={classes.bottomSheet}
          header={
            <div className={classes.calendar_title}>
              <span className={classes.reset} onClick={reset}>
                초기화
              </span>
              <span className={classes.title}>예약찾기</span>
              <span className={classes.close} onClick={popupClose}>
                검색
              </span>
            </div>
          }
          snapPoints={({ maxHeight }) => maxHeight - 56}
        >
          <Box className={classes.fre_wrap}>
            <h2 className={classes.se_title}>기간 설정</h2>
            <RangeCalendar
              onChange={(ranges) => {
                setSelection(ranges["selection"]);
              }}
              ranges={[selection]}
            ></RangeCalendar>
          </Box>

          <Box className={classes.fre_wrap}>
            <h2 className={classes.fre_title}>오피스 설정</h2>
            <Box className={classes.fre_select}>
              <Select100
                options={[
                  { value: "전체 오피스", label: "전체 오피스" },
                  { value: "오피스1", label: "오피스1" },
                  { value: "오피스2", label: "오피스2" },
                  { value: "오피스3", label: "오피스3" },
                ]}
                placeholder="오피스 선택"
                value={office}
                onChange={officeChange}
              />
            </Box>
          </Box>
          {officeSelected && (
            <Box className={classes.fre_wrap}>
              <h2 className={classes.fre_title}>호실 설정</h2>
              <Box className={classes.fre_select}>
                <Select100
                  options={[
                    { value: "전체 호실", label: "전체 호실" },
                    { value: "호실1", label: "호실1" },
                    { value: "호실2", label: "호실2" },
                    { value: "호실3", label: "호실3" },
                  ]}
                  value={room}
                  onChange={setRoom}
                  placeholder="호실 선택"
                />
              </Box>
            </Box>
          )}
        </BottomSheet>
      </Route>
    </Switch>
  );
}
