import React, { useState, useEffect } from "react";
import { useRouteMatch } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";

import { requestBiztApi } from "utility/connectivity";
import MessCard from "appComponents/interaction/MessCard";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",

    backgroundColor: theme.palette.primary.gray,
  },
}));

export default function ChatPage(props) {
  const classes = useStyles();
  const [chat, chatlist] = useState(null);
  let { path } = useRouteMatch();
  function DateForm(date) {
    var dateObj = new Date(date);
    return (
      dateObj.toLocaleDateString("en", { year: "numeric" }) +
      "-" +
      dateObj.toLocaleDateString("en", { month: "2-digit" }) +
      "-" +
      dateObj.toLocaleDateString("en", { day: "2-digit" }) +
      " " +
      dateObj.toLocaleTimeString("it-IT", {
        hour: "2-digit",
        minute: "2-digit",
      })
    );
  }
  useEffect(() => {
    requestBiztApi("/api/message/chat", null, chatlist);
    // console.log(chatlist);
  }, []);
  return (
    <List className={classes.root}>
      {chat &&
        chat.map((chatData, idx) => {
          let cardProps = {
            src: chatData.image,
            title: chatData.chatId,
            body: chatData.content,
            stateCircle: true,
            cancel: true,
            circle: chatData.isRead,
            date: DateForm(chatData.date),
            chatdetail_link: `${path}` + 1,
          };
          return (
            <div>
              <MessCard {...cardProps} />
            </div>
          );
        })}
    </List>
  );
}
