import React from "react";
import { Link } from "react-router-dom";

import { Box, Divider } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import MyReservationCard from "appComponents/interaction/MyReservationCard";
import AddressCopy from "appComponents/interaction/AddressCopy";

import message from "Img/messageColor.svg";

const useStyles = makeStyles((theme) => ({
  plusMinusWrapper: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-around",
    "& > p": {
      width: "50%",
    },

    "& button": {
      // margin: "0 5px",
      boxShadow: "none",
      borderRadius: "0",
    },
  },
  wrapper: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-around",

    "& .MuiGrid-container": {
      flexWrap: "nowrap",
      boxSizing: "border-box",
      flexDirection: "row",
      justifyContent: "space-around",
      alignItems: "center",
    },
    "& .MuiPaper-rounded": {
      borderRadius: 0,
    },
  },
  root: {
    "& .MuiGrid-container": {
      flexWrap: "nowrap",
      boxSizing: "border-box",
      flexDiWhitetion: "row",
      justifyContent: "space-around",
      alignItems: "center",
    },
  },
  detailTitle: {
    color: "black",
    fontWeight: "bold",
  },
  headTitle: {
    fontSize: "18px",
    fontWeight: "bold",

    margin: "20px 0 15px 0 ",
  },
  subDiv: {
    marginBottom: "20px",
  },
  infoDiv: {
    marginBottom: "30px",
  },
  detailDiv: {
    marginBottom: "10px",
  },
  filterWrapper: {
    padding: "20px",
    "& > p": {
      fontWeight: " bold",
    },
  },
  next: {
    backgroundColor: `${theme.palette.primary.main}`,
    color: "white",
    borderRadius: "1px",
    width: "100%",
  },
  spotIcon: {
    width: "15px",
    marginRight: "5px",
  },
  moreButton: {
    color: "darkgray",
    border: "none",
    // backgroundColor: "transparent",
    textAlign: "center",
  },
}));

export default function Myreservation(props) {
  const classes = useStyles();

  return (
    <Box>
      <MyReservationCard
        starController={true}
        classes={classes}
        detail1="이용호실"
        detail2="이용좌석"
        detail3="예약일"
        detail4="예약시간"
        headTitle="방이동 오피스 101"
        detail1_1="개인1호실 / 개인 2호실"
        detail2_1="B - 5"
        detail3_1="21.05.17(월) ~ 21.05.19(수) / 3일간"
        detail4_1="09:00-12:00 / 3시간"
      ></MyReservationCard>
      <Box className="Padding20px">
        <Divider></Divider>
        <Link to="/message/123">
          <Box className="FlexStart" style={{ marginTop: "20px" }}>
            <img className={classes.spotIcon} src={message} alt="" />
            <p className="Padding10px"> 호스트에게 메세지 보내기</p>
          </Box>
        </Link>
      </Box>
      <Box className="BoxGap" style={{ height: "5px" }} />

      <Box className="Padding20px">
        <Box style={{ marginBottom: "10px" }}>
          <p className="bold">예약번호</p>
          <p className=""> HM2RJHF78</p>
        </Box>
        <Divider></Divider>
        <Box style={{ margin: "10px 0" }}>
          <p className="bold">결제액</p>
          <p className="">45,000원</p>
        </Box>
        <Divider></Divider>
        <Box style={{ marginTop: "10px" }}>
          <p className="bold">환불 정책</p>
          <p style={{ color: "blue", cursor: "pointer" }} className="">
            자세히 알아보기
          </p>
        </Box>
      </Box>
      <Box className="BoxGap" style={{ height: "5px" }} />

      {/* <Box className="Padding20px">
        <p className="SubtitleLeft">찾아 가는 방법</p>
        <p className="">서울시 송파구 방이동 889-23 204호</p>
        <Divider style={{ marginTop: "10px" }}></Divider>
        <Box className="FlexStart">
          <img className={classes.spotIcon} src={copy} alt="" />
          <p className="Padding10px">주소 복사하기</p>
        </Box>
      </Box> */}
      <AddressCopy></AddressCopy>
      <Box className="BoxGap" style={{ height: "5px" }} />

      <Box className="Padding20px" style={{ marginBottom: "20px" }}>
        <p style={{ margin: "10px 0 20px" }} className="SubtitleLeft">
          이용 규칙
        </p>
        <div className="SpaceBetween">
          <p className="" style={{ width: "85%" }}>
            이용규칙이용규칙이용규칙이용규칙이용규칙
          </p>
          <div style={{ width: "15%" }}>
            {/* <button className={`${classes.moreButton}`}>더보기</button> */}
          </div>
        </div>
      </Box>
      <Box className="BoxGap" style={{ height: "5px" }} />
    </Box>
  );
}
