import React from "react";
import CarouselSwiper from "appComponents/layout/CarouselSwiper";

import { makeStyles } from "@material-ui/core/styles";
import {
  Box,
  Card,
  CardActionArea,
  CardContent,
  GridList,
  GridListTile,
} from "@material-ui/core";

//메인 상단 배너
const handleDragStart = (e) => e.preventDefault();

// 카드리스트

const mediaCardUseStyles = makeStyles((theme) => ({
  root: {},
  media: {
    height: 150,
  },
  roots: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    flexWrap: "nowrap",
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: "translateZ(0)",
  },
  title: {
    color: theme.palette.primary.light,
  },
  titleBar: {
    background:
      "linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)",
  },
  headDiv: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-between",
  },
  headTitle: {
    fontSize: "25px",
  },
  subTitle: {
    fontSize: "15px",
    color: "darkgray",
    marginRight: "8px",
  },
  subDiv: {
    display: "flex",
    flexWrap: "wrap",
  },
  bookmarkIcon: {
    width: "25px",
  },

  infoDIvWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
    alignItems: "center",
  },
  infoDiv: {
    width: "50%",
  },
  rating: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "flex-start",
  },
  info: {
    marginLeft: "5px",
  },
  rateStar: {
    textAlign: "right",
    boxSizing: "border-box",
  },
  detailDivWrapper: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%",
  },
  detailDiv: {
    width: "100%",
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "flex-end",
  },
  detailTitle: {
    width: "20%",
  },
  detailContents: {
    width: "80%",
  },
  primary: {
    fontSize: "13px",
    padding: "0 5px",
    borderRadius: " 100px",
    marginRight: " 5px",
  },
  wrapper: {
    "& .MuiGridListTile": {
      height: "auto",
      maxWidth: "100%",
      background: "red",
    },
    "& .MuiGridList-root": {
      display: "flex",
      padding: 0,
      flexWrap: "wrap",
      listStyle: "none",
      overflow: "auto",
      flexDirection: "column",
      margin: 0,
    },
    "& .alice-carousel__stage-item img": {
      height: "auto",
      width: "100%",
      maxWidth: "100%",
    },
    "& .MuiPaper-elevation1": {
      boxShadow: "none",
    },
    "& .MuiCardContent-root": {
      padding: "0px",
    },
    "& .swiper-pagination": {
      right: "10px",
      borderRadius: "15px",
    },
  },
  spotIcon: {
    width: "10px",
    marginRight: "5px",
  },
  statusBtn: {
    marginRight: "0px",
    padding: "1px 5px",
    color: `${theme.typography.light}`,
    backgroundColor: `${theme.palette.primary.sub}`,
    borderRadius: "50px",
  },
  moreButton: {
    color: "darkgray",
    border: "none",
    // backgroundColor: "transparent",
  },
}));

const MyReservationCard = (props) => {
  const classes = mediaCardUseStyles(props);

  return (
    <div className={`${classes.wrapper} `}>
      <GridList className={classes.gridList} cols={1}>
        <GridListTile style={{ height: "auto", padding: "20px" }}>
          <Card className={classes.root}>
            <CardActionArea>
              <Box style={{ position: "relative" }}>
                <CarouselSwiper
                  style={{ border: "1px solid red" }}
                  img_array={[
                    <img
                      style={{ width: "100%", height: "100%" }}
                      alt="image1"
                      src="/officeDefault.jpg"
                      className={classes.list_left_img}
                      onDragStart={handleDragStart}
                    />,
                    <img
                      style={{ width: "100%", height: "100%" }}
                      alt="image1"
                      src="/officeSub(1).jpg"
                      className={classes.list_left_img}
                      onDragStart={handleDragStart}
                    />,
                  ]}
                ></CarouselSwiper>
              </Box>
              <CardContent>
                <div className="SpaceBetween">
                  <p className={classes.headTitle}>{props.headTitle}</p>
                  <p className={classes.statusBtn}>예약완료</p>
                </div>

                <div className={classes.detailDivWrapper}>
                  <div className={`${classes.detailDiv}`}>
                    <p className={`${classes.detail} ${classes.detailTitle}`}>
                      {props.detail1}
                    </p>
                    <p
                      className={`${classes.detail} ${classes.detailContents}`}
                    >
                      {props.detail1_1}
                    </p>
                  </div>
                  <div className={`${classes.detailDiv}`}>
                    <p className={`${classes.detail} ${classes.detailTitle}`}>
                      {props.detail2}
                    </p>
                    <p
                      className={`${classes.detail} ${classes.detailContents}`}
                    >
                      {props.detail2_1}
                    </p>
                  </div>
                  <div className={`${classes.detailDiv}`}>
                    <p className={`${classes.detail} ${classes.detailTitle}`}>
                      {props.detail3}
                    </p>
                    <p
                      className={`${classes.detail} ${classes.detailContents}`}
                    >
                      {props.detail3_1}
                    </p>
                  </div>
                  <div className={`${classes.detailDiv}`}>
                    <p className={`${classes.detail} ${classes.detailTitle}`}>
                      {props.detail4}
                    </p>
                    <p
                      className={`${classes.detail} ${classes.detailContents}`}
                    >
                      {props.detail4_1}
                    </p>
                  </div>
                </div>
              </CardContent>
            </CardActionArea>
          </Card>
        </GridListTile>
      </GridList>
    </div>
  );
};

export default MyReservationCard;
