import React from "react";
import { Box } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";

const InfoListExampleStyle = makeStyles((theme) => ({
  list_wrap: {
    width: "100%",
  },

  list_top: {
    width: "100%",
    paddingBottom: "15px",
    position: "relative",
  },
  list_title: {
    fontSize: "18px",
    fontWeight: "500",
    paddingRight: "15px",
    letterSpacing: "-1px",
    paddingBottom: "15px",
  },
  list_btn: {
    boxSizing: "border-box",
    borderRadius: "10px",
    padding: "2px 3px",
    fontWeight: "500",
    marginRight: "5px",
  },

  list_btn3: {
    position: "absolute",
    top: "0px",
    right: "0px",
    fontWeight: "bold",
  },
  list_btn4: {
    position: "absolute",
    top: "-8px",
    right: "-3px",
    fontWeight: "bold",
  },

  txt_left: {
    width: "85px",
    textAlign: "left",
    color: theme.typography.main,
    fontSize: "15px",
    paddingBottom: "13px",
    fontWeight: "500",
  },
  txt_right: {
    color: theme.typography.main,
    fontSize: "15px",
  },
}));

function InfoList(props) {
  const classes = InfoListExampleStyle();
  // const [value, setValue] = React.useState({});
  const [state, setState] = React.useState({
    checkedA: true,
    checkedB: true,
    checkedC: true,
  });
  const { infoTxt } = props;
  return (
    <div>
      <Box className={classes.list_wrap}>
        <Box className={classes.list_top} display="flex">
          <span className={classes.list_title}>{props.list_title}</span>
        </Box>
        <Box className={classes.list_bottom}>
          {infoTxt?.map((infoTxtData, idx) => (
            <Box display="flex" key={idx}>
              <div className={classes.txt_left}>{infoTxtData.txt_left}</div>
              <div className={classes.txt_right}>{infoTxtData.txt_right}</div>
            </Box>
          ))}
        </Box>
      </Box>
    </div>
  );
}
export default InfoList;
