import React from "react";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import Avatar from "@material-ui/core/Avatar";
// 파트너 이미지
import partner_after from "Img/partner_after.svg";
import partner_cancel from "Img/partner_cancel.svg";
import partner_ing from "Img/partner_ing.svg";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",

    backgroundColor: theme.palette.primary.gray,
  },
  inline: {
    display: "inline",
  },
  circle_box: {
    position: "relative",
    width: "65px",
  },
  circle: {
    position: "absolute",
    right: "9px",
    top: "1px",
    width: "6px",
    height: "6px",
    backgroundColor: theme.palette.primary.main,
    borderRadius: "50%",
  },
  circle_img: {
    width: "50px",
    height: "50px",
  },
  title_box: {
    display: "flex",
    paddingTop: "10px",
  },
  title: {
    position: "relative",
    width: "calc(100% - 56px)",
    color: theme.typography.main,
    fontWeight: "500",
    fontSize: "15px",
  },
  title_icon: {
    display: "flex",
    "& div": {
      marginRight: "5px",
    },
  },
  body: {
    paddingTop: "5px",
    fontSize: "13px",
    width: "100%",
    overflow: "hidden",
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
  },
  date: {
    position: "absolute",
    right: "0",
    top: "8px",
    color: theme.typography.dark,
    fontSize: "13px",
    fontWeight: "normal",
  },
  bg_btn: {
    padding: "0 10px 1px 10px",
    backgroundColor: theme.palette.primary.sub,
    fontSize: "12px",
    color: theme.typography.light,
    borderRadius: "10px",
    lineHeight: "155%",
    fontWeight: "normal",
  },
  outline_btn: {
    padding: "0 10px",
    border: `1px solid ${theme.palette.primary.sub}`,
    fontSize: "12px",
    color: theme.typography.main,
    borderRadius: "10px",
    lineHeight: "150%",
    fontWeight: "normal",
  },
}));

const MessCard = (props) => {
  const classes = useStyles();
  var onOff = props.circle;
  const { src } = props;

  var cancel = props.cancel;
  var stateCircle = props.stateCircle;

  return (
    <List style={{ padding: "5px 23px 5px 20px" }}>
      <Link to={props.chatdetail_link} style={{ textDecoration: "none" }}>
        <ListItem alignItems="flex-start">
          <ListItemAvatar className={classes.circle_box}>
            <Avatar alt="Remy Sharp" src={src} className={classes.circle_img} />
            {onOff && <span className={classes.circle}></span>}
          </ListItemAvatar>
          <div className={classes.title}>
            <div className={classes.title_box}>
              <div>{props.title}</div>
              <div className={classes.title_icon}>
                {stateCircle ? (
                  <div className={classes.bg_btn}>승인요청중</div>
                ) : (
                  <div className={classes.bg_btn}>승인 완료</div>
                )}
                {cancel && <div className={classes.outline_btn}>예약 취소</div>}
              </div>
            </div>
            <div className={classes.body}>{props.body}</div>
            <span className={classes.date}>{props.date}</span>
          </div>
        </ListItem>
      </Link>
    </List>
  );
};
export default MessCard;
