import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import { ListItem, ListItemAvatar, Avatar } from "@material-ui/core";

import InfoList from "appComponents/layout/InfoList";
import MessButton from "pages/Messages/MessButton";

// RCE CSS
import "react-chat-elements/dist/main.css";

const PartnerStyles = makeStyles((theme) => ({
  card_wrap: {
    backgroundColor: theme.palette.primary.gray,
    paddingBottom: "10px",
    position: "relative",
  },
  card: {
    paddingTop: "30px",
    paddingBottom: "20px",
  },
  circle_box: {
    position: "relative",
    display: "flex",
    justifyContent: "flex-end",
    alignItems: "center",
  },

  title: {
    position: "relative",
    width: "calc(100% - 56px)",
    color: theme.typography.main,
    fontWeight: theme.typography.fontWeightMedium,
    textAlign: "right",
    paddingRight: "15px",
    fontSize: "15px",
    paddingTop: "10px",
  },
  other_title: {
    position: "relative",
    width: "calc(100% - 56px)",
    color: theme.typography.main,
    fontWeight: theme.typography.fontWeightMedium,
    textAlign: "left",
    paddingLeft: "15px",
    fontSize: "15px",
    paddingTop: "10px",
  },
  body: {
    width: "100%",
    paddingTop: "4px",
    fontSize: "13px",
    fontWeight: "normal",
    wordBreak: "break-all",
  },
  date_left: {
    position: "absolute",
    left: "0",
    top: "5px",
    color: theme.typography.dark,
    fontSize: "13px",
    fontWeight: "normal",
  },
  date_right: {
    position: "absolute",
    right: "0",
    top: "5px",
    color: theme.typography.dark,
    fontSize: "13px",
    fontWeight: "normal",
  },
  circle_img: {
    width: "60px",
    height: "60px",
  },
  info_box: {
    width: "95%",
    margin: "10px auto 0 auto",
    padding: "20px",
    backgroundColor: theme.typography.light,
  },
  oneButton: {
    width: "100%",
    border: `1px solid ${theme.typography.main}`,
    padding: "15px",
    borderRadius: "10px",
    marginTop: "20px",
  },
}));
const MessDetail = (props) => {
  const classes = PartnerStyles();
  const { who, src, title, date, body, info_card, twoButton } = props;
  //사용자 who==user/ 상대방 who==other
  return (
    <div className={classes.card_wrap}>
      {who == "user" ? (
        <ListItem alignItems="flex-end" className={classes.card}>
          <div className={classes.title}>
            <div className={classes.id}>{title}</div>
            <div className={classes.body}>{body}</div>
            <span className={classes.date_left}>{date}</span>
          </div>
          <ListItemAvatar className={classes.circle_box}>
            <Avatar alt="Remy Sharp" src={src} className={classes.circle_img} />
          </ListItemAvatar>
        </ListItem>
      ) : (
        <ListItem alignItems="flex-start" className={classes.card}>
          <ListItemAvatar className={classes.circle_box}>
            <Avatar alt="Remy Sharp" src={src} className={classes.circle_img} />
          </ListItemAvatar>
          <div className={classes.other_title}>
            <div className={classes.id}>{title}</div>
            <div className={classes.body}>{body}</div>
            <span className={classes.date_right}>{date}</span>
          </div>
        </ListItem>
      )}

      {info_card && (
        <div className={classes.info_box}>
          <InfoList
            list_title={props.list_title}
            infoTxt={props.infoTxt}
          ></InfoList>
          {twoButton ? (
            <MessButton></MessButton>
          ) : (
            <div className="FlexCenter">
              <button className={classes.oneButton}>
                {props.reservationStatus}
              </button>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default MessDetail;
