import { useState } from "react";
import { Link } from "react-router-dom";
import { Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { withStyles } from "@material-ui/core/styles";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import OnOffButton from "pages/Partner/Reservation/OnOffButton";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
// 파트너 이미지
import mess from "Img/mess.svg";

const Accordion = withStyles({
  root: {
    boxShadow: "none",
    "&:not(:last-child)": {
      borderBottom: 0,
    },
    "&:before": {
      display: "none",
    },
    "&$expanded": {
      margin: "auto",
    },
  },
  expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
  root: {
    marginBottom: 0,
    minHeight: "auto",
    "&$expanded": {
      minHeight: 0,
    },
  },
  content: {
    margin: "0",
    "&$expanded": {
      margin: "0",
    },
  },
  expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: 0,
    width: "100%",
  },
}))(MuiAccordionDetails);

const OfficeListButtonStyle = makeStyles((theme) => ({
  list_wrap: {
    width: "100%",
  },
  list_box: {
    width: "100%",
    display: "flex",
  },
  list_left: {
    width: "28%",
    padding: "20px 0px 20px 16px",
  },
  list_left_img: {
    width: "100px",
    height: "100px",
  },
  list_right: {
    width: "72%",
    color: theme.typography.main,
    paddingLeft: "16px",
    position: "relative",
  },
  list_top: {
    width: "100%",
    paddingBottom: "15px",
    position: "relative",
  },
  list_title: {
    fontSize: "15px",
    fontWeight: "500",
    paddingRight: "15px",
    letterSpacing: "-1px",
  },
  list_btn: {
    boxSizing: "border-box",
    borderRadius: "10px",
    padding: "2px 3px",
    fontWeight: "500",
    marginRight: "5px",
    display: "flex",
    "& div": {
      marginRight: "3px",
    },
  },
  after: {
    width: "60px",
    marginRight: "5px",
  },
  before: {
    width: "53px",
    marginRight: "5px",
  },
  cancel: {
    width: "62px",
    marginRight: "5px",
  },
  ing: {
    width: "69px",
    marginRight: "5px",
  },
  use: {
    width: "50px",
    marginRight: "5px",
  },
  review: {
    width: "70px",
    marginRight: "5px",
  },
  yes: {
    width: "60px",
    marginRight: "5px",
  },

  list_right_btn: {
    position: "absolute",
    top: "-2px",
    right: "-20px",
    fontWeight: "bold",
  },
  list_room: {
    fontSize: "14px",
  },
  list_rate: {
    verticalAlign: "-4px",
  },

  list_txt_box: {
    paddingLeft: "8px",
  },
  list_txt: {
    fontSize: "14px",
    color: theme.typography.main,
    fontWeight: "500",
  },
  txt_left: {
    width: "75px",
    textAlign: "left",
    color: theme.typography.dark,
    fontSize: "13px",
    paddingBottom: "4px",
    wordSpacing: "1px",
    letterSpacing: "1px",
  },
  txt_right: {
    color: theme.typography.main,
    fontSize: "13px",
    fontWeight: "500",
  },
  list_bottom_btn: {
    position: "absolute",
    right: "18px",
    bottom: "15px",
  },
  list_bottom_btn_img: {
    width: "24px",
    height: "24px",
  },
  detail: {
    width: "85px",
    height: "22px",
    color: theme.typography.light,
    fontSize: "14px",
    fontWeight: "500",
    borderRadius: "10px",
    marginTop: "8px",
    padding: "2px 7px",
    background:
      "linear-gradient(90deg, rgba(252,100,14,1) 0%, rgba(254,84,61,1) 47%, rgba(254,62,109,1) 100%)",
    textDecoration: "none",
  },
  button_wrap: {
    width: "100%",
    padding: "5px 16px 15px 16px",
    display: "flex",
    justifyContent: "space-around",
  },
  bg_btn: {
    padding: "1px 10px 2px 10px",
    backgroundColor: theme.palette.primary.sub,
    fontSize: "12px",
    color: theme.typography.light,
    borderRadius: "9px",
    lineHeight: "155%",
    boxSizing: "content-box",
  },
  outline_btn: {
    padding: "0px 10px",
    border: `1px solid ${theme.palette.primary.sub}`,
    fontSize: "12px",
    color: theme.typography.main,
    borderRadius: "9px",
    lineHeight: "150%",
    boxSizing: "content-box",
  },
}));

function ReservationCard(props) {
  const classes = OfficeListButtonStyle();
  // const [value, setValue] = useState({});
  const [expand, expand_change] = useState(true);

  var { status, review } = props;
  var { title, img, cardDetails, mess_link } = props;
  const [expanded, setExpanded] = useState("panel1");

  const expandShow = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
    expand_change(newExpanded ? false : true);
  };

  function renderCircle(status) {
    switch (status) {
      case "ing": {
        return <div className={classes.outline_btn}>승인요청</div>;
      }
      case "before": {
        return <div className={classes.outline_btn}>입실전</div>;
      }
      case "cancel": {
        return <div className={classes.outline_btn}>예약취소</div>;
      }
      case "use": {
        return <div className={classes.outline_btn}>이용중</div>;
      }
      case "after": {
        return <div className={classes.outline_btn}>퇴실완료</div>;
      }
      default: {
        return null;
      }
    }
  }

  function renderButton(status, review) {
    if (status === "before") {
      return <OnOffButton classes={classes}>예약 취소</OnOffButton>;
    } else {
      if (review) {
        return <OnOffButton classes={classes}>리뷰 작성</OnOffButton>;
      }
    }
  }

  return (
    <div>
      <Accordion
        square
        expanded={expanded === "panel2"}
        onChange={expandShow("panel2")}
      >
        <Box className={classes.list_wrap}>
          <div className={classes.list_box}>
            <Box className={classes.list_left}>
              <img
                src={img}
                alt="reservation"
                className={classes.list_left_img}
              ></img>
            </Box>
            <Box p={2} className={classes.list_right}>
              <Box className={classes.list_top} display="flex">
                <span className={classes.list_title}>{title}</span>
                <div className={classes.list_btn}>
                  {renderCircle(status)}
                  {review && <div className={classes.bg_btn}>사용자리뷰</div>}
                </div>

                <div className={classes.list_right_btn}>
                  {(status === "before" || review) && (
                    <AccordionSummary
                      aria-controls="panel1d-content"
                      id="panel1d-header"
                    >
                      {expand && (
                        <ExpandMoreIcon
                          className={classes.expand_icon}
                        ></ExpandMoreIcon>
                      )}
                      {!expand && (
                        <ExpandLessIcon
                          className={classes.expand_icon}
                        ></ExpandLessIcon>
                      )}
                    </AccordionSummary>
                  )}
                </div>
              </Box>
              <Box className={classes.list_bottom}>
                {cardDetails?.map((detail, idx) => (
                  <Box key={idx} display="flex">
                    <div className={classes.txt_left}>{detail.category}</div>
                    <div className={classes.txt_right}>{detail.content}</div>
                  </Box>
                ))}
                <div className={classes.list_bottom_btn}>
                  <Link to={mess_link}>
                    <img
                      className={classes.list_bottom_btn_img}
                      src={mess}
                      alt="대화창"
                    ></img>
                  </Link>
                </div>
              </Box>
            </Box>
          </div>
        </Box>
        <AccordionDetails>
          <Box className={classes.button_wrap}>
            {renderButton(status, review)}
          </Box>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}

export default ReservationCard;
