import React, { useState, useEffect } from "react";
import clsx from "clsx";
import { Link } from "react-router-dom";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import { HowToRegRounded } from "@material-ui/icons";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import ResDeny from "pages/Messages/ResDeny";
const useStyles = makeStyles((theme) => ({
  buttonWrap:{
    width: "100%",
    display: "flex",
    justifyContent: "space-around",
    paddingTop: "10px",
   
  },
  button: {
    height: "47px",
    display: "flex",
    flexGrow: "1",
    justifyContent: "center",
    alignItems: "center",
    fontSize: "14px",
    fontWeight: theme.typography.fontWeightMedium,
    textDecoration: "none",
    borderRadius: "8px",
    border: `1px solid ${theme.palette.primary.main}`,
    backgroundColor: theme.typography.light,
    color: theme.palette.primary.main,
    "&:hover": {
      border: `1px solid ${theme.palette.primary.main}`,
      backgroundColor: theme.typography.light,
      color: theme.palette.primary.main,
      boxShadow: "none",
    },
    "&:nth-child(2)": {
      marginLeft:"10px"
    }
  },

  colorchange: {
    border: `1px solid ${theme.palette.primary.main}`,
    backgroundColor: theme.palette.primary.main,
    color: theme.typography.light,
    "&:hover": {
      border: `1px solid ${theme.palette.primary.main}`,
      backgroundColor: theme.palette.primary.main,
      color: theme.typography.light,
      boxShadow: "none",
    },
  },
  popup: {
    width: "100%",
    "& .MuiDialog-container": {
      width: "100%",
    },
  },
}));

const buttonEnum = {
  left: 0,
  right: 1,
};

function CustomButton({label, onClick }) {
  const classes = useStyles();
  return (
    <Button
      color="primary"
      // className={clsx(
      //   classes.button,
      //   value === tag ? classes.colorchange : null
      // )}
      className={classes.button}
      onClick={onClick}
    >
      {label}
    </Button>
  );
}

function MessButton(props) {
  const classes = useStyles();
 
  const [open, setOpen] = React.useState(false);
  const [error, setError] = React.useState();


  const [btnState,setBtnState] = useState("notSelected");

  const denyBtn = () => {
    setOpen(true);
  };
  const approveBtn =()=>{
    setBtnState("approveSelected")
  }
  const denyCancelBtn =()=>{
    setBtnState("notSelected")
  }
  const handleSubmit = (txt) => {
    console.log(txt);
    if (!txt) {
      setError("* 거부사유를 입력해주세요");
    } else {
      setOpen(false);
      setError("");
      setBtnState("denySelected")
    }
  };
  const handleClose = (txt) => {
    setOpen(false);
    setError("");
  };
  function renderSwitch(btnState) {
    switch (btnState){
      case "notSelected" :
        return (
        <div className={classes.buttonWrap}>
          <CustomButton
            label="승인"
            onClick={approveBtn}/>
          <CustomButton
            label="거부"
            onClick={denyBtn}/>
        </div>);
      case "approveSelected" :
        return (
          <CustomButton label="승인취소/거부"
          onClick={denyBtn}/>
        );
      case "denySelected" :
        return ( 
          <CustomButton label="거부취소"
          onClick={denyCancelBtn} />
        );
    }
  }
  
  return (
    <div>
      <div className={classes.buttonWrap}>
      {renderSwitch(btnState)}
      </div>
      {/* 거부사유 팝업 */}
      <Dialog
        disableScrollLock
        open={open}
        fullWidth={true}
        onClose={handleClose}
        className={classes.popup}
        aria-labelledby="form-dialog-title"
      >
        <ResDeny
          error={error}
          close={handleClose}
          submit={(txt) => {
            handleSubmit(txt);
          }}
        ></ResDeny>
      </Dialog>
    </div>
  );
}
export default MessButton;
