import React, { useState, useEffect } from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";
import PropTypes from "prop-types";

import { makeStyles } from "@material-ui/core/styles";
import { Paper, Tabs, Tab, Box, useTheme } from "@material-ui/core";
import SearchDetail from "pages/User/Search/searchDetailComponents/SearchDetail";
import MyReservation from "pages/User/MyReservation";

import ReviewWrite from "pages/User/ReviewWrite";

import {
  renderReservationCard,
  SearchCard,
} from "appComponents/interaction/CarouselCard";

import { onResponseSuccess, requestBiztApi } from "utility/connectivity";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexFlow: "column",
    flexGrow: 1,
    height: "100%",
    "& .MuiPaper-elevation1": {
      boxShadow: "none",
    },
    "& .MuiPaper-rounded": {
      borderRadius: "0",
      border: `1px solid ${theme.palette.primary.lightdark}`,
    },
  },
  tabContainer: {
    flex: "0 0 48px",
    zIndex: 1,
    background: "white",
    "& .MuiTabs-flexContainer": {
      display: "flex",
      justifyContent: "space-around",
    },
    "& img": {
      marginTop: "10px",
    },
  },
  TabPanelContainer: {
    overflowY: "auto",
    flex: "1 1 auto",
  },
  content: {
    "& .MuiBox-root": {
      padding: "0px",
    },
  },
  TabButton: {
    minWidth: "0",
  },
}));

export default function Favorite() {
  const classes = useStyles();
  const { path } = useRouteMatch();
  const theme = useTheme();

  const [selected, setSelected] = useState(0);
  const [reservationData, setReservationData] = useState(null);
  const [favoriteData, setFavoriteData] = useState(null);
  const [viewedData, setViewedData] = useState(null);

  const handleChange = (event, newValue) => {
    setSelected(newValue);
  };

  useEffect(() => {
    switch (selected) {
      case TabMenu.RESERVATION:
        requestBiztApi(
          "/api/reservation",
          null,
          onResponseSuccess(setReservationData)
        );
        break;
      case TabMenu.FAVORITE:
        // favorite에 대한 무언가를 url에 추가해야함
        requestBiztApi("/api/office", null, onResponseSuccess(setFavoriteData));
        break;
      case TabMenu.VIEWED:
        // 로컬 스토리지 이용할까
        let dataStr = window.localStorage.getItem("recent-view");
        if (dataStr) {
          setViewedData(JSON.parse(dataStr));
        }
        break;
      default:
        console.error("invalid tab selection: ", selected);
    }
  }, [selected]);

  return (
    <Switch>
      <Route path={`${path}/([0-9]+)`} component={SearchDetail}></Route>
      <Route path={`${path}/review/([0-9]+)`} component={ReviewWrite}></Route>
      <Route
        path={`${path}/myreservation/([0-9]+)`}
        component={MyReservation}
      ></Route>
      <Route path={path}>
        <div className={classes.root}>
          <Paper className={classes.tabContainer}>
            <Tabs
              value={selected}
              onChange={handleChange}
              indicatorColor="primary"
              textColor="primary"
              variant="fullWidth"
              style={{
                borderBottom: `1px solid ${theme.palette.primary.lightdark}`,
              }}
            >
              <Tab label="예약조회" className={classes.TabButton} />
              <Tab label="찜목록" className={classes.TabButton} />
              <Tab label="최근검색" className={classes.TabButton} />
            </Tabs>
          </Paper>
          <Box className={classes.TabPanelContainer}>
            <TabPanel className={classes.content} value={selected} index={0}>
              {renderReservationCard(reservationData, path, theme)}
            </TabPanel>
            <TabPanel className={classes.content} value={selected} index={1}>
              {favoriteData
                ? favoriteData.map((item, index) => {
                    return <SearchCard key={index} {...item} />;
                  })
                : "검색 결과가 없습니다 :("}
            </TabPanel>
            <TabPanel className={classes.content} value={selected} index={2}>
              {viewedData
                ? viewedData
                    .map((item, index) => {
                      return <SearchCard key={index} {...item} />;
                    })
                    .reverse()
                : "검색 결과가 없습니다 :("}
            </TabPanel>
          </Box>
        </div>
      </Route>
    </Switch>
  );
}

const TabMenu = {
  RESERVATION: 0,
  FAVORITE: 1,
  VIEWED: 2,
};
